<template>
  <v-stepper tile v-model="e1" class="mb-2">
    <v-stepper-header >
      <v-stepper-step :complete="e1 > 1" step="1">정보 입력</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 2" step="2">장소 입력</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="3">사진 첨부</v-stepper-step>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        <v-card class="mb-2">
          <v-layout class="pt-2">
            <v-flex xs12>
              <v-text-field
              ref="ad_title"
              v-model="ad_title"
              :rules="ad_title_rules"
              :eroor-messages="errorMessages"
              label="제목"
              outlined
              clearable
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs6 md6 >
              <v-text-field
              :class="pr"
              ref="ad_honorarium"
              v-model="ad_honorarium"
              :rules="ad_honorarium_rules"
              :eroor-messages="errorMessages"
              label="사례금"
              :disabled="!ad_swich"
              clearable
              outlined
              ></v-text-field>
            </v-flex>
            <v-flex xs6 md6 class="d-flex pl-12">
                <v-switch class="pa-1  mt-3"
                    v-model="ad_swich"
                    :label="adSwichString"
                  ></v-switch>
            </v-flex>
          </v-layout>
          <v-layout wrap>
            <v-flex xs12 md6>
              <v-text-field
              :class="pr"
              ref="ad_pone"
              v-model="ad_pone"
              :rules="ad_pone_rules"
              :eroor-messages="errorMessages"
              label="연락번호"
              outlined
              clearable
              @blur="adPoneChange"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 md6>
            <v-dialog
              ref="dialog"
              v-model="calender"
              :return-value.sync="ad_date"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  :class="pl"
                  ref="ad_date"
                  v-model="ad_date"
                  label="실종 날짜"
                  readonly
                  outlined
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="ad_date" scrollable locale="ko">
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="calender = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(ad_date)">OK</v-btn>
              </v-date-picker>
            </v-dialog>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs12>
              <v-text-field
                  ref="ad_place"
                  v-model="ad_place"
                  :rules="ad_place_rules"
                  :eroor-messages="errorMessages"
                  label="실종 장소"
                  outlined
                  readonly
                  clearable
                  @click="test"
                  >
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs12>
              <v-textarea
              ref="ad_detail"
              v-model="ad_detail"
              label="반려견의 특징이나 상세 내용을 입력해주세요."
              :rules="ad_detail_rules"
              :eroor-messages="errorMessages"
              outlined>
              </v-textarea>
            </v-flex>
          </v-layout>
        </v-card>
        <v-btn color="primary" @click="step1">다음</v-btn>
      </v-stepper-content>
      <v-stepper-content step="2">
        <v-card class="mb-12" color="grey lighten-1" height="200px"></v-card>
        <!-- <v-btn color="primary" @click="e1 ++ ">다음</v-btn> -->
        <v-btn color="primary" @click="test">다음</v-btn>
    <v-btn text @click="e1 --">이전</v-btn>
      </v-stepper-content>
      <v-stepper-content step="3">
        <v-card class="mb-12" color="grey lighten-1" height="200px"></v-card>
        <!-- <v-btn color="primary" @click="e1 ++ ">다음</v-btn> -->
        <v-btn color="primary" @click="test">다음</v-btn>
    <v-btn text @click="e1 --">이전</v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
        <!-- <KakaoMap ref="kakaoMap"/> -->
</template>

<script>
  import KakaoMap from '../KakaoMap'
  export default {
    data: () => ({
      ad_title:null, //제목
      ad_honorarium:null, //사례금
      ad_swich : false, //사례금 여부
      ad_pone:null, //연락 번호
      ad_detail:null, //상세내용
      ad_date:new Date().toISOString().substr(0, 10),//실종 일자
      ad_place : null,
      ad_place_rules : [
        v => !!v || "실종 장소를 입력해주세요."
      ],
      errorMessages:null, 
      ad_title_rules:[ // 제목 체크사항
        v => v === null ? '제목이 입력되지 않았습니다' 
        : v.length > 32 ? '32자 이내로 입력해주세요.' 
        : v.replace(/\s/g, '').length !== 0 || "공백만 입력하셨습니다."
      ],
      ad_honorarium_rules:[ //사례금 체크사항
        v => !!v || '사례금을 입력해 주세요.',
        v => Number.isInteger(v*1) || '숫자만 입력해주세요.'
      ],
      ad_pone_rules:[ // 연락번호 체크사항
        v => v === null ? '연락가능한 번호를 입력하세요.'
        : v.length < 15 || '유효하지않은 번호 양식입니다.'
      ],
      ad_detail_rules:[// 상세내용 체크사항
        v => v === null ? '상세내용을 입력해주세요.' 
        : v.replace(/\s/g, '').length !== 0 || "공백만 입력하셨습니다."
      ],
      e1:1, //스텝퍼의 위치
      calender: false, //달력 on/off 변수
      formHasErrors: false,//폼의 에러 발생 여부
    }),
    mounted () {
    },
    components: {
      KakaoMap,
    },
    methods: {
      //스텝퍼의 각 단계마다 다음 버튼 클릭시 실행
      // 1페이지 일때 폼 내부의 에러 사항 확인 후 없으면
      // 다음 스텝퍼 이동, 있으면 에러메세지를 띄워줌
      step1(){
        this.formHasErrors = false;

        Object.keys(this.form).forEach(f => {
          if(!this.form[f] || this.$refs[f].hasError){
            this.formHasErrors = true;
            this.$refs[f].validate(true);
          }
        })

        if(this.ad_swich){
          if(!this.ad_ad_honorarium || this.$refs["ad_honorarium"].hasError){
            this.formHasErrors = true;
            this.$refs["ad_honorarium"].validate(true);
          }
        }

        if(!this.formHasErrors){
          this.e1 ++;
        }

      },
      test1(){
        this.ab_dialog = true;
        console.log(this.$refs);
      },
      test(){
        alert("클릭!");
      },
      // 연락번호 입력후 blur시에 실행되는 메소드
      // 정수 부분만 추출한 뒤 추출된 문자열에 따라 정규식을 적용하여 형변환
      adPoneChange(){
        if(!this.$refs["ad_pone"].hasError){
          let extraction = this.ad_pone.replace(/[^0-9]/g,'');
          let format_pone = ''; 
          let pone_bl = false;
          let regular = '';
          if(extraction.length === 11){ //11자리
            format_pone = extraction.replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3');
            regular = /^\d{3}-\d{3,4}-\d{4}$/;
          }else if(extraction.length === 8){ //8자리
            format_pone = extraction.replace(/(\d{4})(\d{4})/, '$1-$2');
            regular = /^\d{4}-\d{4}$/;
          }else if(extraction.length === 12 && extraction.indexOf('05') === 0){
            format_pone = extraction.replace(/(\d{4})(\d{4})(\d{4})/, '$1-$2-$3');
            regular = /^\d{4}-\d{4}-\d{4}$/;
          }else{
            if(extraction.indexOf('02') === 0){
              format_pone = extraction.length < 10 ?  extraction.replace(/(\d{2})(\d{3})(\d{4})/, '$1-$2-$3') :  extraction.replace(/(\d{2})(\d{4})(\d{4})/, '$1-$2-$3');
              regular = /^\d{2}-\d{3,4}-\d{4}$/;
            }else{
              format_pone = extraction.replace(/(\d{3})(\d{3})(\d{4})/, '$1-$2-$3');
              regular = /^\d{3}-\d{3,4}-\d{4}$/;
            }
          }
          pone_bl = regular.test(format_pone);
          if(!pone_bl){
            this.$refs["ad_pone"].errorBucket.push("잘못된 번호입니다.");
            return false;
          }
          this.ad_pone = format_pone;
        }
      }
    },
    computed: {
      // 사례금 선택시 택스트 변환
      adSwichString(){
        return this.ad_swich ? '(단위 : 만원)' : '없음';
      },
      // 폼 데이터, 사례금 제외
      form(){
        return{
          ad_title : this.ad_title,
          ad_pone : this.ad_pone,
          ad_date : this.ad_date,
          ad_detail : this.ad_detail,
        }
      },
      //연락번호와 실종날짜 폼 breakpoint에 따라 간격 변경
      pr(){
        return this.$vuetify.breakpoint.xs || this.$vuetify.breakpoint.sm ? "pr-0" : "pr-1"
      },
      pl(){
        return this.$vuetify.breakpoint.xs || this.$vuetify.breakpoint.sm ? "pl-0" : "pl-1"
      }
      
    }
  }
</script>